import * as CANNON from "cannon-es";
import { Vec3 } from "cannon-es";
import CannonUtils from "../utils/physics_utils/cannonUtils.js";

export default class PhysicsManager {
  constructor(context) {
    this.context = context;
    this.initWorld();
    this.initCharachterCollider();
  }

  initWorld() {
    this.context.world = new CANNON.World({
      // gravity: new CANNON.Vec3(0, -109.82, 0),
    });

    // this.context.world.allowSleep = true;

    this.context.world.defaultContactMaterial.contactEquationStiffness = 1e9;

    // Stabilization time in number of timesteps
    this.context.world.defaultContactMaterial.contactEquationRelaxation = 4;

    this.solver = new CANNON.GSSolver();
    this.solver.iterations = 7;
    this.solver.tolerance = 0.1;
    this.context.world.solver = new CANNON.SplitSolver(this.context.solver);
    // use this.context to test non-split solver
    this.context.world.solver = this.solver;

    this.context.world.gravity.set(0, -9.8, 0);

    this.physicsMaterial = new CANNON.Material("physics");
    this.physics_physics = new CANNON.ContactMaterial(
      this.physicsMaterial,
      this.physicsMaterial,
      {
        friction: 0.0,
        restitution: 0.3,
      }
    );

    this.context.world.addContactMaterial(this.physics_physics);

    this.groundBody = new CANNON.Body({
      type: CANNON.Body.STATIC, // can also be achieved by setting the mass to 0
      shape: new CANNON.Plane(),
    });
    // this.groundBody.position.y = 0.6607888340950012;
    this.groundBody.quaternion.setFromEuler(-Math.PI / 2, 0, 0); // make it face up
    this.context.world.addBody(this.groundBody);
  }

  initCharachterCollider() {
    this.radius = 0.425;
    this.sphereShape = new CANNON.Sphere(this.radius);
    this.context.sphereBody = new CANNON.Body({
      mass: 1,
      material: this.physicsMaterial,
    });
    this.context.sphereBody.addShape(this.sphereShape);
    this.context.sphereBody.position.set(-2, 3, -1);
    this.context.sphereBody.linearDamping = 0.9;
    this.context.world.addBody(this.context.sphereBody);
  }

  cannonifyMeshAutoHull(
    meshToBeCanonified,
    hullType,
    bodyType,
    bodyMaterial,
    bodyMass
  ) {
    let canonifiedMesh = threeToCannon(meshToBeCanonified, { type: hullType });

    let { shape } = canonifiedMesh;

    let cannonifiedBody = new CANNON.Body({
      mass: bodyMass,
      type: bodyType,
      shape,
      material: bodyMaterial,
    });

    cannonifiedBody.position.copy(meshToBeCanonified.position);
    cannonifiedBody.quaternion.copy(meshToBeCanonified.quaternion);

    this.context.world.addBody(cannonifiedBody);
  }

  cannonifyMeshGeometry(
    meshToBeCanonified,
    bodyType,
    bodyMaterial,
    bodyMass,
    scale = new Vec3(1, 1, 1)
  ) {
    let shape = CannonUtils.CreateTrimesh(meshToBeCanonified.geometry);
    shape.scale.copy(scale);

    // shape.scale.set(0.75, 0.75, 0.75);
    let meshBody = new CANNON.Body({
      type: bodyType,
      material: bodyMaterial,
      mass: bodyMass,
    });

    meshBody.addShape(shape);
    meshBody.position.copy(meshToBeCanonified.position);
    meshBody.quaternion.copy(meshToBeCanonified.quaternion);

    this.context.world.addBody(meshBody);
  }

  cannonifyMeshWithCustomConvexHull(
    customConvexHullGeometry,
    meshToBeCanonified
  ) {
    console.log("HERE");

    console.log("meshToBeCanonified");
    console.log(meshToBeCanonified);
    console.log("customConvexHullGeometry");
    // console.log(customConvexHullGeometry);

    // customConvexHullGeometry.scale.setScalar(0.999);

    let shape = CannonUtils.CreateTrimesh(customConvexHullGeometry.geometry);
    // shape.scale.copy(meshToBeCanonified.scale);

    let customConvexHullBody = new CANNON.Body({
      // shape,
      // mass: 0,
      material: this.physicsMaterial,
    });

    customConvexHullBody.addShape(shape);

    customConvexHullBody.position.copy(meshToBeCanonified.position);
    customConvexHullBody.quaternion.set(0, 0, 0, 1);

    console.log(customConvexHullBody);

    this.context.world.addBody(customConvexHullBody);
  }
}
